@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@100;200;300;400;500;600;700&family=Monoton&family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Secular+One&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nunito+Sans:ital,wght@0,200;0,600;1,900&display=swap");
@import url("https://rsms.me/inter/inter.css");
* {
  margin: 0px;
  padding: 0px;
}
.home-page {
  background: linear-gradient(
      to right,
      rgba(0, 0, 0, 0.5),
      rgba(0, 0, 0, 0.7),
      rgba(0, 0, 0, 0.9)
    ),
    url("../public/gb1.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  height: 100vh;
  width: 100%;
  margin: auto;
}
.hero-section {
  display: flex;
  margin: auto;
  /* justify-content: center; */
  align-items: center;
  /* margin-top: 3rem; */
}
.hero-section p {
  color: rgb(208, 208, 208);
  font-size: 1rem;
  font-weight: 200;
  font-family: "Nunito", "Inter", "Raleway", "Segoe UI", Tahoma, Geneva, Verdana,
    sans-serif;
  width: 70%;
  margin-top: 1rem;
  text-align: justify;
}
.hero-section h1 {
  margin-top: 3.3rem;
  color: rgb(236, 236, 236);
  font-size: 4.5rem;
  font-weight: 900;
  font-family: "Nunito", "Inter", "Raleway", "Segoe UI", Tahoma, Geneva, Verdana,
    sans-serif;
}
.canvas-wrapper {
  width: 35vw;
  height: 65vh;
  /* position: absolute; */
  display: flex;
  /* justify-content: center;
  align-items: center; */
}
.threed-model-section {
  position: relative;
  margin-left: 2.2in;
  margin-top: 6em;
  text-align: center;
}
.auto-typing {
  color: white;
  display: flex;
  margin-top: 3.5rem;
}
.auto-typing h3{
  font-size: 1.5rem;
  font-weight: 100;
  font-family: "Raleway", "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}
.navigate-section h4 {
  color: rgb(179, 179, 179);
  font-family: 'Raleway','Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-weight: 300;
  /* margin-top: 5rem;
  margin-bottom: 2rem; */
}
.navigate-section a{
  text-decoration: none;

}
.navigate-section {
  background: white;
  display: flex;
  background: transparent;
  box-shadow: 8px 8px 20px 2px rgb(0, 0, 0);
  color: white;
  backdrop-filter: blur(1px);
  background: linear-gradient(
    -45deg,
    rgba(255, 255, 255, 0.19),
    rgb(155, 155, 155, 0.2)
  );
  color: white;
  padding: 1.6em;
  padding-top: -1em;
  gap: 3em;
  width: 40%;
  margin-bottom: 1.3rem;
  border-radius: 1rem;
  justify-content: center;
}
.navbar-section{
  background: transparent;
  -webkit-backdrop-filter: blur(30px);
  backdrop-filter: blur(3px);  
  padding: 2em;
  background: linear-gradient(
    -45deg,
    rgba(255, 255, 255, 0.09),
    rgb(155, 155, 155, 0.02)
  );
  font-family: 'Inter','Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-weight: 100;
}
.navbar-section h1{
  color: white;
}
.navbar-section nav{
  display: flex;
  justify-content: space-between;
}
.nav-items a{
  text-decoration: none;
  color: white;
  font-size: 1.2rem;
  font-weight: 100;
  margin-left: 1em;
  font-family: 'Nunito','Raleway','Inter','Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}


/* For mobile devices with a width of up to 576px */
@media screen and (max-width: 576px) {
  .hero-section {
    flex-direction: column;
  }
  .hero-section h1 {
    font-size: 2.5rem;
  }
  .hero-section p {
    font-size: 0.9rem;
    width: 90%;
  }
  .canvas-wrapper {
    width: 80%;
    height: 40vh;
    margin: auto;
    margin-top: 1rem;
  }
  .threed-model-section {
    margin-left: 0;
    margin-top: 2rem;
  }
  .navigate-section {
    width: 90%;
    padding: 1em;
    gap: 1em;
  }
  .auto-typing{
    /* margin: auto; */
    /* text-align: center; */
    justify-content: center;
    display: flex;
  }
}



/* For mobile devices with a width of up to 768px */
@media screen and (max-width: 768px) {
  .hero-section h1 {
    font-size: 3rem;
  }
  .hero-section p {
    font-size: 1rem;
    width: 80%;
  }
  .canvas-wrapper {
    width: 60%;
    height: 50vh;
  }
}

/* For mobile devices with a width of up to 992px */
@media screen and (max-width: 992px) {
  .hero-section h1 {
    font-size: 3.5rem;
  }
}

/* For mobile devices with a width of up to 1200px */
@media screen and (max-width: 1200px) {
  .hero-section h1 {
    font-size: 4rem;
  }
}

/* 320px - 576px */
@media only screen and (min-width: 320px) and (max-width: 576px) {
  .home-page {
    background-position: center;
    background-size: cover;
    margin-top: 0px;
    background-color: black;
  }
  .hero-section h1 {
    font-size: 3rem;
    /* margin: auto; */
    text-align: center;
  }
  .hero-section p {
    font-size: 0.8rem;
    width: 90%;
    margin: auto;
    margin-top: 1rem;
  }
  .canvas-wrapper {
    width: 78vw;
  }
  .threed-model-section {
    margin-left: 0;
    margin-top: 2em;
  }
  .auto-typing h3{
    font-size: 1.2rem;
  }
  .navigate-section {
    /* flex-direction: column; */
    width: 75%;
    gap: 1em;
    padding: 1em;
    margin: auto;
  }
  .navbar-section h1{
    font-size: 1rem;
    margin: auto;
    align-items: center;
    justify-content: center;
  }
  .nav-items p{
    font-size: 0.1rem;
  }
}
